.Contact{border-left:3px solid #adabab;min-height:50vh;position:relative}#contact-info{display:flex;justify-content:center;align-items:center;height:30vh}#contact-info .links{display:flex;justify-content:space-between}@media all and (min-width: 768px){#contact-info .links{min-width:500px}}#contact-info .links a{font-size:20px;text-decoration:none;color:#adabab;display:block;position:relative}#contact-info .links a::after{content:"";position:absolute;top:0;left:0;width:100%;height:100%;border:1px solid transparent;transition:all 0.3s}#contact-info .links a::after:hover{--transformed-box-diagonal-length: 164%;width:var(--transformed-box-diagonal-length);left:calc((100% - var(--transformed-box-diagonal-length)) / 2)}@media all and (min-width: 768px){#contact-info .links a{font-size:32px}}#contact-info .links a div{position:relative;display:block;width:30px;height:30px;transition:transform 0.3s;margin:5px}@media all and (min-width: 768px){#contact-info .links a div{width:55px;height:55px}}#contact-info .links a span{position:absolute;top:0;left:0;width:100%;height:100%;transition:0.5s;border:1px solid #adabab;border-radius:5px}#contact-info .links a span:nth-child(5){display:flex;justify-content:center;align-items:center;position:relative}#contact-info .links a:hover div{transform:rotate(-35deg) skew(20deg)}#contact-info .links a:hover span:nth-child(5){transform:translate(20px, -20px);opacity:1}#contact-info .links a:hover span:nth-child(4){transform:translate(15px, -15px);opacity:0.8}#contact-info .links a:hover span:nth-child(3){transform:translate(10px, -10px);opacity:0.6}#contact-info .links a:hover span:nth-child(2){transform:translate(5px, -5px);opacity:0.4}#contact-info .links a:hover span:nth-child(1){opacity:0.2}

.ScrollBtn{width:35px;height:35px;bottom:20px;right:20px;position:fixed;border-radius:50px;background:lightgray;opacity:0.6;z-index:8}.ScrollBtn:hover{opacity:0.8}.ScrollBtn:hover a{color:white}@media all and (min-width: 768px){.ScrollBtn{width:40px;height:40px;bottom:30px;right:30px}}.ScrollBtn a{width:100%;height:100%;display:flex;justify-content:center;align-items:center;cursor:pointer}.ScrollBtn a i{font-size:18px}

.Footer{background:lightgrey;text-align:center;color:white;padding:5px;font-size:small}.Footer .mawii{width:15px;height:15px}

